.container {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  width: 100%;
  @media screen and (min-width: 320px) {
    width: 320px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
  @media screen and (min-width: 1024px) {
    width: 1024px;
  }
}
.cards-collection {
  padding-top: 20px;
  padding-bottom: 40px;
  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -32px;
    margin-bottom: -32px;
  }
  @media screen and (min-width: 1024px) {
    margin-left: -16px;
  }
}

.cards-collection-item {
  cursor: pointer;
  transform: scale(1);
  transition: transform 250ms linear;
  border-radius: 5px;
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
    margin-left: 32px;
    flex-basis: calc((100% - 2 * 32px) / 2);
  }
  @media screen and (min-width: 1024px) {
    margin-left: 16px;
    flex-basis: calc((100% - 3 * 16px) / 3);
  }
}

.cards-collection-item:hover,
.cards-collection-item:focus {
  transform: scale(1.03);
  box-shadow: 0px 4px 4px rgba(245, 124, 50, 0.12),
    0px 4px 4px rgba(245, 124, 50, 0.12), 1px 4px 6px rgba(245, 124, 50, 0.12);
}

.card-poster img {
  width: 100%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 5px;
  margin-bottom: 10px;
  height: 402px;
  @media screen and (min-width: 768px) {
    height: 455px;
    margin-bottom: 8px;
  }
  @media screen and (min-width: 1024px) {
    height: 449px;
  }
}

.card-info {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

.card-title {
  text-transform: uppercase;
  color: black;
}

.card-text {
  color: orange;
}

.card-poster {
  position: relative;
}

.card-vote-average {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 20px;
  background-color: orange;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}
